@use 'sass:map';

$textSizes: (
  "medium": 1.875vw, // 1.875vw - calculated
  "small": 1.250vw,
  "tiny": 0.938vw
);

@function textSize($key){
  @return map.get($breakpoints, $key);
}

$breakpoints: (
  "phone": 568px,
);

@function breakpoint($key){
  @return map.get($breakpoints, $key);
}

$colors: (
  "dark-4": #10002B,
  "dark-3": #240046,
  "dark-2": #3C096C,
  "dark-1": #5A189A,
  "light-1": #7B2CBF,
  "light-2": #9D4EDD,
  "light-3": #C77DFF,
  "light-4": #E0AAFF,
  "light-5": #f8ebff,
  "accent": #70BF2C
);

@function color($key){
  @return map.get($colors, $key);
}

* {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  margin: 0;
  box-sizing: border-box;

  color: color("light-5");
}

html {
  body {
    overflow-x: hidden;
    background-color: color("dark-4");
  }
}
